.nav-bar-container {
    background-color: #FFDCA9;
    color: #FAAB78;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    height: 6vh;
}

.logo {
    font-size: 5vh;
    font-family: 'Archivo', sans-serif;
    font-weight: 900;
    position: relative;
    left: 15px;
    transition: all 0.1s ease-in;
}

.logo:hover {
    transform: scale(1.1);
    cursor: pointer;
    color: #90F1B7;
}

.nav-bar {
    font-size: 2.5vh;
    font-family: 'Inter', sans-serif; 
    font-weight: 700;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 135px;
    gap: 2.5vh;

    span {
        transition: all 0.1s ease-in;
    }

    span:hover {
        transform: scale(1.1);
        cursor: pointer;
        color: #90F1B7;
    }
}

.gear {
    height: 2.75vh;
    width: 2.75vh; 
    color: #FAAB78;
    position: relative;
    right: 20px;
    transition: all 0.1s ease-in;
}

.gear:hover {
    transform: scale(1.1);
    cursor: pointer;
    color: #90F1B7;
}

.bars {
    display: none;
    height: 2.75vh;
    width: 2.75vh; 
    color: #FAAB78;
    position: relative;
    right: 20px;
    transition: all 0.1s ease-in;
}

.bars:hover {
    transform: scale(1.1);
    cursor: pointer;
    color: #90F1B7;
}

.settings {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

@media only screen and (max-width: 786px) {
    .logo {
        font-size: 4vh;
    }

    .nav-bar {
        font-size: 1.75vh;
    }
}

@media only screen and (max-width: 480px) {
    .nav-bar {
        display: none;
    }

    .bars {
        display: block;
    }

    .gear {
        display: none;
    }
}