.inside-pantry-container {
    height: 45vh;
    width: 65vw;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5vw;
    justify-content: center;
    align-items: center;
    overflow-y: scroll;

    .pantry-items {
        border: 3px solid #FAAB78;
        padding: 1vw;
        border-radius: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1vw;

        span {
            font-size: 2.5vh;
            font-family: "Inter", sans-serif;
            font-weight: 500;
            color: #FAAB78;
        }

        .delete-pantry-item {
            font-size: 2.5vh;
            color: #FAAB78;
            transition: all 0.1s ease-in-out;
        }

        .delete-pantry-item:hover {
            transform: scale(1.1);
            color: #90F1B7;
            cursor: pointer;
        }
        


    }
}

